import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from 'axios';
import { AuthService } from './auth';

// ÿ•ŸÜÿ¥ÿßÿ° instance ŸÖŸÜ axios ŸÖÿπ ÿ•ÿπÿØÿßÿØÿßÿ™ ŸÖÿÆÿµÿµÿ©
const createApiClient = (): AxiosInstance => {
  const client = axios.create({
    baseURL: '/api',
    timeout: 10000,
    headers: {
      'Content-Type': 'application/json',
    },
  });

  // ÿ•ÿ∂ÿßŸÅÿ© interceptor ŸÑŸÑÿ∑ŸÑÿ®ÿßÿ™ ŸÑÿ•ÿ±ŸÅÿßŸÇ ÿßŸÑÿ™ŸàŸÉŸÜ ÿ™ŸÑŸÇÿßÿ¶ŸäÿßŸã
  client.interceptors.request.use(
    (config) => {
      const token = AuthService.getToken();
      console.log('üîç Token retrieved:', token ? 'Found' : 'Not found', token?.substring(0, 20) + '...');
      
      if (token) {
        config.headers.Authorization = `Bearer ${token}`;
        console.log('‚úÖ Authorization header set');
      } else {
        console.log('‚ùå No token found, Authorization header not set');
      }
      
      console.log('API Request:', {
        method: config.method?.toUpperCase(),
        url: config.url,
        headers: config.headers,
        data: config.data
      });
      
      return config;
    },
    (error) => {
      console.error('Request interceptor error:', error);
      return Promise.reject(error);
    }
  );

  // ÿ•ÿ∂ÿßŸÅÿ© interceptor ŸÑŸÑÿßÿ≥ÿ™ÿ¨ÿßÿ®ÿßÿ™ ŸÑŸÑÿ™ÿπÿßŸÖŸÑ ŸÖÿπ ÿßŸÑÿ£ÿÆÿ∑ÿßÿ°
  client.interceptors.response.use(
    (response: AxiosResponse) => {
      console.log('API Response:', {
        status: response.status,
        statusText: response.statusText,
        data: response.data
      });
      return response;
    },
    (error) => {
      console.error('Response interceptor error:', error);
      
      // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ÿßŸÑÿÆÿ∑ÿ£ 401 (ÿ∫Ÿäÿ± ŸÖÿµÿ±ÿ≠)ÿå ŸÇŸÖ ÿ®ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿÆÿ±Ÿàÿ¨
      if (error.response?.status === 401) {
        console.log('Unauthorized access, logging out...');
        AuthService.logout();
        
        // ÿ•ÿπÿßÿØÿ© ÿ™Ÿàÿ¨ŸäŸá ÿ•ŸÑŸâ ÿµŸÅÿ≠ÿ© ÿ™ÿ≥ÿ¨ŸäŸÑ ÿßŸÑÿØÿÆŸàŸÑ ŸÖÿπ ŸÖÿ±ÿßÿπÿßÿ© ÿßŸÑŸÄ locale
        if (typeof window !== 'undefined') {
          const currentPath = window.location.pathname;
          const locale = currentPath.split('/')[1] || 'ar'; // ÿßŸÅÿ™ÿ±ÿßÿ∂Ÿä ÿßŸÑÿπÿ±ÿ®Ÿäÿ©
          window.location.href = `/${locale}/login`;
        }
      }
      
      return Promise.reject(error);
    }
  );

  return client;
};

// ÿ•ŸÜÿ¥ÿßÿ° client ŸÑŸÑÿßÿ≥ÿ™ÿÆÿØÿßŸÖ ÿßŸÑÿπÿßŸÖ
export const apiClient = createApiClient();

// ÿ•ŸÜÿ¥ÿßÿ° client ŸÖÿÆÿµÿµ ŸÑŸÑÿßÿ™ÿµÿßŸÑ ÿßŸÑŸÖÿ®ÿßÿ¥ÿ± ÿ®ŸÄ NeoSend API
export const neoSendClient = axios.create({
  baseURL: 'https://neosending.com/api',
  timeout: 15000,
  headers: {
    'Content-Type': 'application/json',
    'Accept': 'text/plain',
    'User-Agent': 'NeoSend-Client/1.0'
  },
});

// ÿ•ÿ∂ÿßŸÅÿ© interceptor ŸÑŸÄ NeoSend client
neoSendClient.interceptors.request.use(
  (config) => {
    const token = AuthService.getToken();
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    
    console.log('NeoSend API Request:', {
      method: config.method?.toUpperCase(),
      url: config.url,
      headers: config.headers
    });
    
    return config;
  },
  (error) => {
    console.error('NeoSend request error:', error);
    return Promise.reject(error);
  }
);

neoSendClient.interceptors.response.use(
  (response) => {
    console.log('NeoSend API Response:', {
      status: response.status,
      statusText: response.statusText,
      data: response.data
    });
    return response;
  },
  (error) => {
    console.error('NeoSend response error:', error);
    return Promise.reject(error);
  }
);

// ÿØŸàÿßŸÑ ŸÖÿ≥ÿßÿπÿØÿ© ŸÑŸÑÿßÿ≥ÿ™ÿÆÿØÿßŸÖ ÿßŸÑÿ≥ŸáŸÑ
export const api = {
  // ÿ∑ŸÑÿ®ÿßÿ™ GET
  get: <T = any>(url: string, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return apiClient.get(url, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ POST
  post: <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return apiClient.post(url, data, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ PUT
  put: <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return apiClient.put(url, data, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ DELETE
  delete: <T = any>(url: string, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return apiClient.delete(url, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ PATCH
  patch: <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return apiClient.patch(url, data, config);
  }
};

// ÿØŸàÿßŸÑ ŸÑŸÑÿ™ÿπÿßŸÖŸÑ ŸÖÿπ NeoSend API ŸÖÿ®ÿßÿ¥ÿ±ÿ©
export const neoSendApi = {
  // ÿ∑ŸÑÿ®ÿßÿ™ GET
  get: <T = any>(url: string, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return neoSendClient.get(url, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ POST
  post: <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return neoSendClient.post(url, data, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ PUT
  put: <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return neoSendClient.put(url, data, config);
  },
  
  // ÿ∑ŸÑÿ®ÿßÿ™ DELETE
  delete: <T = any>(url: string, config?: AxiosRequestConfig): Promise<AxiosResponse<T>> => {
    return neoSendClient.delete(url, config);
  }
};

export default apiClient;
